.MoreOptionsListCnt{
    width: 80%;
    height: 80%;
    margin: 2rem auto;
    border: 2px solid white;
    overflow: auto;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.4),-10px -10px 10px rgba(0, 0, 0, 0.2);
    background-image:linear-gradient(151.05deg,#02ccba,#a951ed);
    border-radius: 10px;
}
.MoreOptionsList{
    list-style-type: square;

}
.MoreOptionsList li{
    padding: 1rem;
    color: white;
}