.Sidebar {
  width: 100%;
  background-color: white;
  display: flex;
  padding: 1rem;
  box-sizing: border-box;
  overflow: scroll;
  flex-wrap: wrap;
  flex-direction: row;
}

@media screen and (max-width: 999px) {
  .heading {
    width: 100%;
    display: inline-block;
  }
  .Sidebar .Sidebar__searchBox {
    width: 100%;
  }
  .Sidebar__differentSections ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-evenly; */
  }
  .Sidebar__differentSections a,li {
    width: 100%;
    display:block;
    margin:.2rem 0;
  }
}
.Sidebar h3 {
  text-align: center;
}
.heading,
.Sidebar ul li {
  background-image: linear-gradient(151.05deg, #02ccba, #a951ed);
  border-radius: 10px;
  color: white;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.4);
}

.Sidebar input[type="text"] {
  border-radius: 10px;
  display: block;
  width: 100%;
  margin: 1rem auto;
  box-sizing: border-box;
  border: none;
  background-color: white;
  border-bottom: 1px solid rgb(252, 125, 6);
  padding: 0.5rem;
}

.Sidebar input[type="text"]:focus {
  border: 3px solid rgb(252, 125, 6);
  outline: none;
}

.Sidebar ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.Sidebar ul li {
  padding: 0.5rem;
  text-align: center;
  margin: 0;
  display: flex;
  align-items: center;
  margin: 2rem 0;
}
.Sidebar ul li img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 0.5rem;
}
.Sidebar ul a {
  text-decoration: none;
  color: black;
}

.Sidebar ul li:hover {
  background-color: black;
  color: white;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.4);
  padding: 0.5rem;
  border-radius: 15px;
}

@media screen and (min-width: 1000px) {
  .Sidebar {
    flex-direction: column;
    display: flex;
    width: 25%;
    overflow: scroll;
    flex-wrap: nowrap;
    height: 100vh;
  }
  .Sidebar > div {
    display: block;
  }
  .Sidebar__differentSections {
    display: flex;
    width: 100%;
    flex-direction: column;
  }
}
